@import "_normalize";
@import "_variables";
@import "_mixins";
@import "_hamburgers";


html {
    box-sizing: border-box;
}

*,
*:before,
*:after {
    box-sizing: inherit;
}

body {
    font-family: $main-font, sans-serif;
}

// nav
.navigation {
    position: fixed;
    top: 0;
    height: 8vh;
    width: 100vw;
    background-color: #fff;
    z-index: 1;

    // mobile landscape
    @media(min-width: 533px) and (orientation: landscape) {
        height: 16vh;
    }

    @media(min-width: 730px) and (orientation: landscape) {
        height: 12vh;
    }

    @media(min-width: 900px) and (orientation: landscape) {
        height: 8vh;
    }

    // desktops
    @media(min-width: 1024px) and (orientation: landscape) {
        height: 5vh;
    }

    &__list {
        position: absolute;
        height: 0;
        top: 100%;
        left: 0;
        right: 0;
        margin: 0;
        padding-left: 16px;
        padding-bottom: 8px;
        background-color: #fff;
        list-style: none;
        transition: 0.25s;
        font-size: 0;

        // desktops
        @media(min-width: 1024px) and (orientation: landscape) {
            height: 5vh;
            position: absolute;
            top: 0;
            display: flex;
            justify-content: flex-start;
            align-items: center;
            padding: 0 0 0 16px;
            font-size: 18px;
        }

        &--visible {
            height: 15vh;
            font-size: 16px;

            // mobile portrait
            @media(min-width: 480px) and (orientation: portrait) {
                height: 12vh;
                padding-top: 8px;
                font-size: 18px;
            }

            @media(min-width: 600px) and (orientation: portrait) {
                height: 14vh;
                padding-top: 16px;
                font-size: 22px;
            }

            @media(min-width: 768px) and (orientation: portrait) {
                height: 16vh;
                font-size: 30px;
            }

            // mobile landscape
            @media(min-width: 533px) and (orientation: landscape) {
                height: 26vh;
            }

            @media(min-width: 730px) and (orientation: landscape) {
                height: 20vh;
            }

            @media(min-width: 900px) and (orientation: landscape) {
                height: 15vh;
                font-size: 18px;
            }
        }
    }

    &__item {

        // desktops
        @media(min-width: 1024px) and (orientation: landscape) {
            transition: 0.5s;
            height: 100%;

            &:hover {
                background-color: #dedede;
            }
        }
    }

    &__link {
        color: rgba(0, 0, 0, .5);
        text-decoration: none;

        // desktops
        @media(min-width: 1024px) and (orientation: landscape) {
            transition: 0.5s;
            display: block;
            width: 100%;
            height: 100%;
            padding: 0 16px;
            display: flex;
            align-items: center;
        }

        &:hover {
            color: #000;
        }
    }

    &__button {
        position: absolute;
        top: 0;
        right: 0;
        transform: scale(.75);
        border-style: none;
        background-color: #fff;

        // mobile portrait
        @media(min-width: 480px) and (orientation: portrait) {
            top: 50%;
            transform: scale(1) translateY(-50%);
        }

        @media(min-width: 768px) and (orientation: portrait) {
            top: 25%;
            right: 2%;
            transform: scale(1.25);
        }

        // mobile landscape
        @media(min-width: 533px) and (orientation: landscape) {
            // top: -20%;
            // right: -2%;
        }

        // desktops
        @media(min-width: 1024px) and (orientation: landscape) {
            display: none;
        }
    }
}

// welcome
.welcome {
    height: 80vh;
    margin-top: calc(10vh - 8px);
    padding-left: 16px;
    padding-right: 16px;
    background-image: url('../assets/img/top-bg.jpg');
    background-position: 40% 50%;
    color: #fff;
    text-align: center;
    @include flex-column;
    align-items: center;

    // mobile portrait
    @media(min-width: 768px) and (orientation: portrait) {
        margin-top: calc(10vh - 16px);
    }

    // mobile landscape
    @media(min-width: 533px) and (orientation: landscape) {
        height: 95vh;
    }

    // desktops
    @media(min-width: 1024px) and (orientation: landscape) {
        height: 70vh;
        margin-top: 5vh;
        background-size: cover;
    }

    &__title {
        padding-top: 10vh;
        margin-bottom: 0;
        font-size: 30px;
        text-transform: uppercase;

        // mobile portrait
        @media(min-width: 480px) and (orientation: portrait) {
            font-size: 40px;
        }

        @media(min-width: 768px) and (orientation: portrait) {
            font-size: 55px;
        }

        // mobile landscape
        @media(min-width: 533px) and (orientation: landscape) {
            font-size: 25px;
        }

        @media(min-width: 850px) and (orientation: landscape) {
            font-size: 40px;
        }

        // desktops
        @media(min-width: 1024px) and (orientation: landscape) {
            margin-top: 0;
        }
    }

    &__advantage {
        color: $orange;
    }

    &__description {
        margin-top: 8px;
        font-size: 14px;
        line-height: 1.5;

        // mobile portrait
        @media(min-width: 480px) and (orientation: portrait) {
            font-size: 18px;
        }

        @media(min-width: 768px) and (orientation: portrait) {
            font-size: 24px;
        }

        // mobile landscape
        @media(min-width: 533px) and (orientation: landscape) {
            font-size: 12px;
        }

        @media(min-width: 850px) and (orientation: landscape) {
            font-size: 16px;
        }
    }

    &__button {
        margin-top: 12vh;
        padding: 10px 20px;
        border-style: none;
        border-radius: 4px;
        background-color: $orange;
        color: #fff;
        text-align: center;

        // mobile portrait
        @media(min-width: 480px) and (orientation: portrait) {
            margin-top: 15vh;
            padding: 12px 24px;
            font-size: 24px;
        }

        @media(min-width: 768px) and (orientation: portrait) {
            padding: 20px 40px;
            font-size: 35px;
            border-radius: 16px;
        }

        // mobile landscape
        @media(min-width: 533px) and (orientation: landscape) {
            margin-top: 22vh;
        }

        @media(min-width: 850px) and (orientation: landscape) {
            margin-top: 15vh;
            padding: 12px 30px;
            font-size: 20px;
            border-radius: 12px;
        }

        // desktops
        @media(min-width: 1024px) and (orientation: landscape) {
            transition: 0.5s;

            &:hover {
                background-color: rgba(256, 208, 7, 0.5);
                cursor: pointer;
            }
        }
    }
}

// offer
.offer {
    padding: 0 16px 64px 16px;
    text-align: center;

    // mobile portrait
    @media(min-width: 480px) and (orientation: portrait) {
        padding-left: 64px;
        padding-right: 64px;
    }

    @media(min-width: 768px) and (orientation: portrait) {
        padding-left: 100px;
        padding-right: 100px;
    }

    // mobile landscape
    @media(min-width: 533px) and (orientation: landscape) {
        padding-left: 64px;
        padding-right: 64px;
    }

    @media(min-width: 850px) and (orientation: landscape) {
        padding-left: 150px;
        padding-right: 150px;
    }

    // desktops
    @media(min-width: 1024px) and (orientation: landscape) {
        padding-left: 16px;
        padding-right: 16px;
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
    }

    &__title {
        margin: 10vh 0 10vh 0;
        font-size: 26px;
        text-transform: uppercase;

        // mobile portrait
        @media(min-width: 480px) and (orientation: portrait) {
            font-size: 35px;
        }

        @media(min-width: 600px) and (orientation: portrait) {
            font-size: 38px;
        }

        @media(min-width: 768px) and (orientation: portrait) {
            font-size: 50px;
        }

        // mobile landscape
        @media(min-width: 850px) and (orientation: landscape) {
            font-size: 34px;
        }

        // desktops
        @media(min-width: 1024px) and (orientation: landscape) {
            flex-basis: 100%;
            font-size: 38px;
        }
    }

    &__item {
        margin-bottom: 6vh;

        // desktops
        @media(min-width: 1024px) and (orientation: landscape) {
            flex-basis: 30%;
            padding-left: 32px;
            padding-right: 32px;
        }
    }

    &__image {

        // mobile portrait
        @media(min-width: 768px) and (orientation: portrait) {
            width: 20%;
        }
    }

    &__name {
        margin-top: 10px;
        font-size: 22px;

        // mobile portrait
        @media(min-width: 480px) and (orientation: portrait) {
            margin-bottom: 10px;
            font-size: 28px;
        }

        @media(min-width: 600px) and (orientation: portrait) {
            font-size: 32px;
        }

        @media(min-width: 768px) and (orientation: portrait) {
            font-size: 40px;
        }

        // mobile landscape
        @media(min-width: 850px) and (orientation: landscape) {
            font-size: 26px;
        }
    }

    &__description {
        line-height: 1.5;

        // mobile portrait
        @media(min-width: 480px) and (orientation: portrait) {
            font-size: 18px;
        }

        @media(min-width: 768px) and (orientation: portrait) {
            font-size: 24px;
        }

        // mobile landscape
        @media(min-width: 850px) and (orientation: landscape) {
            font-size: 18px;
        }
    }
}

// opinion
.opinion {
    height: 80vh;
    padding-top: 5vh;
    background-image: url('../assets/img/testimonial-bg.jpg');
    background-position: 5% 25%;
    color: #fff;
    text-align: center;

    // mobile portrait
    @media(min-width: 360px) and (orientation: portrait) {
        height: 65vh;
    }

    @media(min-width: 480px) and (orientation: portrait) {
        height: 55vh;
    }

    @media(min-width: 600px) and (orientation: portrait) {
        padding-top: 2vh;
        height: 50vh;
    }

    // mobile landscape
    @media(min-width: 533px) and (orientation: landscape) {
        height: 110vh;
    }

    @media(min-width: 640px) and (orientation: landscape) {
        padding-top: 10vh;
    }

    @media(min-width: 730px) and (orientation: landscape) {
        height: 100vh;
        padding-top: 15vh;
    }

    @media(min-width: 850px) and (orientation: landscape) {
        height: 80vh;
        padding-top: 8vh;
    }

    // desktops
    @media(min-width: 1024px) and (orientation: landscape) {
        padding-top: 4vh;
        height: 50vh;
        background-attachment: fixed;
        background-size: cover;
    }

    &__title {
        margin-bottom: 16px;
        font-size: 42px;
        text-transform: uppercase;

        // mobile portrait
        @media(min-width: 600px) and (orientation: portrait) {
            font-size: 50px;
        }

        @media(min-width: 768px) and (orientation: portrait) {
            font-size: 60px;
        }

        // desktops
        @media(min-width: 1024px) and (orientation: landscape) {
            font-size: 52px;
        }
    }

    &__span {
        color: $orange;
    }

    &__quote {

        // mobile portrait
        @media(min-width: 480px) and (orientation: portrait) {
            margin: 16px 80px;
        }

        @media(min-width: 600px) and (orientation: portrait) {
            margin: 16px 120px;
        }

        @media(min-width: 768px) and (orientation: portrait) {
            margin: 16px 150px;
        }

        // mobile landscape
        @media(min-width: 533px) and (orientation: landscape) {
            margin: 16px 100px;
        }

        @media(min-width: 640px) and (orientation: landscape) {
            margin: 16px 150px;
        }

        @media(min-width: 730px) and (orientation: landscape) {
            margin: 16px 200px;
        }

        // desktops
        @media(min-width: 1024px) and (orientation: landscape) {
            max-width: 716px;
            margin: 40px auto;
        }
    }

    &__content {
        line-height: 1.5;

        // mobile portrait
        @media(min-width: 480px) and (orientation: portrait) {
            font-size: 18px;
        }

        @media(min-width: 600px) and (orientation: portrait) {
            font-size: 20px;
        }

        @media(min-width: 768px) and (orientation: portrait) {
            font-size: 24px;
        }

        // desktops
        @media(min-width: 1024px) and (orientation: landscape) {
            font-size: 18px;
        }
    }

    &__author {

        // mobile portrait
        @media(min-width: 600px) and (orientation: portrait) {
            font-size: 20px;
        }

        @media(min-width: 768px) and (orientation: portrait) {
            font-size: 24px;
        }
    }

    &__break-line {
        @media(min-width: 1024px) and (orientation: landscape) {
            display: none;
        }
    }
}

// portfolio
.portfolio {
    padding: 32px 16px;
    background-color: $portfolio-background;

    // desktops
    @media(min-width: 1024px) and (orientation: landscape) {
        padding: 64px 200px;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
    }

    &__title {
        margin-top: 0;
        text-transform: uppercase;
        text-align: center;

        // mobile portrait
        @media(min-width: 600px) and (orientation: portrait) {
            font-size: 38px;
        }

        @media(min-width: 768px) and (orientation: portrait) {
            font-size: 50px;
        }

        // desktops
        @media(min-width: 1024px) and (orientation: landscape) {
            margin-bottom: 64px;
            flex-basis: 100%;
            font-size: 38px;
        }
    }

    &__project {
        margin-bottom: 16px;

        // desktops
        @media(min-width: 1024px) and (orientation: landscape) {
            flex-basis: 48%;
        }
    }

    &__image {
        width: 100%;
        margin-top: 8px;
        margin-bottom: 16px;
        border-radius: 8px;
    }

    &__span {
        @include span-text($red);

        // mobile portrait
        @media(min-width: 600px) and (orientation: portrait) {
            font-size: 16px;
        }

        @media(min-width: 768px) and (orientation: portrait) {
            font-size: 20px;
        }
    }

    &__project-name {
        margin-top: 8px;
        text-transform: uppercase;

        // mobile portrait
        @media(min-width: 600px) and (orientation: portrait) {
            margin-bottom: 12px;
            font-size: 24px;
        }

        @media(min-width: 768px) and (orientation: portrait) {
            font-size: 30px;
        }
    }

    &__description {
        line-height: 1.5;

        // mobile portrait
        @media(min-width: 600px) and (orientation: portrait) {
            font-size: 18px;
        }

        @media(min-width: 768px) and (orientation: portrait) {
            font-size: 22px;
        }
    }

    &__button {
        margin-top: 8px;
        padding: 8px 32px;
        border-style: none;
        border-radius: 4px;
        background-color: $red;
        color: #fff;
        transition: 0.5s;

        // mobile portrait
        @media(min-width: 600px) and (orientation: portrait) {
            font-size: 20px;
        }

        @media(min-width: 768px) and (orientation: portrait) {
            font-size: 30px;
        }

        // desktops
        &:hover {
            @media(min-width: 1024px) and (orientation: landscape) {
                cursor: pointer;
                background-color: $red-hover;
            }
        }
    }
}

// contact
.contact {
    position: relative;
    padding: 16px 16px 32px 16px;

    // mobile landscape
    @media(min-width: 533px) and (orientation: landscape) {
        padding-left: 64px;
        padding-right: 64px;
        max-width: 1520px;
        margin: 0 auto;
    }

    // desktops
    @media(min-width: 1024px) and (orientation: landscape) {
        padding-left: 200px;
        padding-right: 200px;
    }

    &__span {
        @include span-text($green);

        // mobile portrait
        @media(min-width: 600px) and (orientation: portrait) {
            font-size: 16px;
        }
    }

    &__title {
        margin-top: 8px;
        text-transform: uppercase;

        // mobile portrait
        @media(min-width: 600px) and (orientation: portrait) {
            font-size: 38px;
        }
    }

    &__label {
        display: block;
        margin-bottom: 8px;
        color: $label-grey;
        font-size: 10px;

        // mobile portrait
        @media(min-width: 600px) and (orientation: portrait) {
            font-size: 14px;
        }
    }

    &__input {
        width: 90%;

        // mobile landscape
        @media(min-width: 533px) and (orientation: landscape) {
            width: 60%;
        }

        // desktops
        @media(min-width: 1024px) and (orientation: landscape) {
            width: 25%;
        }
    }

    &__button {
        padding: 8px 20px;
        border-style: none;
        border-radius: 4px;
        background-color: $green;
        color: #fff;
        transition: 0.5s;

        // mobile portrait
        @media(min-width: 600px) and (orientation: portrait) {
            font-size: 20px;
        }

        // desktops
        @media(min-width: 1024px) and (orientation: landscape) {
            &:hover {
                cursor: pointer;
                background-color: $green-hover;
            }
        }
    }

    &__image {
        display: none;

        // desktops
        @media(min-width: 1024px) and (orientation: landscape) {
            display: inline-block;
            width: 40%;
            position: absolute;
            top: 16px;
            right: 20%;
            z-index: -1;
        }
    }
}

// info
.info {
    position: relative;
    padding: 16px;
    background-color: $blue;
    color: #fff;
    font-weight: 300;

    // mobile portrait
    @media(min-width: 768px) and (orientation: portrait) {
        padding: 32px;
    }

    // desktops
    @media(min-width: 1024px) and (orientation: landscape) {
        height: 50vh;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    &__list {
        padding-left: 0;
        list-style: none;

        // desktops
        @media(min-width: 1024px) and (orientation: landscape) {
            margin-right: 64px;
        }
    }

    &__list-item {
        margin-bottom: 16px;
        margin-right: 16px;

        // mobile portrait
        @media(min-width: 600px) and (orientation: portrait) {
            font-size: 20px;
        }

        @media(min-width: 768px) and (orientation: portrait) {
            font-size: 24px;
        }

        &--link {
            padding-bottom: 16px;
            border-bottom: 1px solid $border-grey;

            &:first-of-type {
                padding-top: 16px;
            }

            &:last-of-type {
                border-style: none;
            }
        }

        &--contact {
            @media(min-width: 1024px) and (orientation: landscape) {
                margin-bottom: 24px;
            }
        }
    }

    &__span {
        padding-right: 8px;
        color: $orange;
    }

    &__icon {
        padding-right: 8px;
        color: #000;
    }

    &__link {
        color: #fff;
        text-decoration: none;

        // desktops
        &:hover {
            @media(min-width: 1024px) and (orientation: landscape) {
                text-decoration: underline;
            }
        }
    }

    &__image {
        width: 100%;

        // mobile landscape
        @media(min-width: 533px) and (orientation: landscape) {
            display: block;
            max-width: 400px;
            margin: 0 auto;
        }

        // desktops
        @media(min-width: 1024px) and (orientation: landscape) {
            margin: 0;
        }
    }
}

// copyright
.copyright {
    height: 10vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: $orange;
    color: #fff;
}